import { ChangeEvent } from 'react';

import Card from 'components/ui/Card';
import Typography from 'components/ui/Typography';
import RadioGroup from 'components/forms/RadioGroup';

import type { RadioProps } from 'components/forms/Radio';

export interface PortfolioTabsProps {
  label: string;
  options: Pick<RadioProps, 'value' | 'label' | 'disabled'>[];
  onChange?: (e: ChangeEvent<HTMLInputElement>) => void;
  defaultValue?: string | number;
}

const PortfolioTabs = ({ label, options, onChange, defaultValue }: PortfolioTabsProps) => {
  return (
    <Card hasShadow gap={12} pt="pt-4" pb="pb-6" px="px-7">
      <Typography size="lg" weight={700} color="text-gray-700">
        {label}
      </Typography>
      <RadioGroup
        name={label}
        options={options}
        defaultValue={defaultValue}
        onChange={onChange}
        gap="gap-3"
        fullWidth
      />
    </Card>
  );
};

export default PortfolioTabs;
